import React, { useState } from 'react';
import './navstyle.css';
import {motion}  from "framer-motion";
import { SidebarData } from './Data';
import Icon from 'react-icons-kit';
import {user_circle} from 'react-icons-kit/ikons/user_circle';
import {logout} from 'react-icons-kit/ikons/';
import {menu} from 'react-icons-kit/iconic/menu'


const Navbar = () => {

    const [selected,setSelected] =useState(0);
    const[expended,setExpended] =useState(true);

    const studentLogout = () => {
        JSON.parse(localStorage.removeItem('auth'));
      }

    const sidebarVariant ={
        true:{left:"0%"},
        false:{left:"-60%"}
    }

  return (
    <>

     <div className="bars" style={expended?{left:"60%"}: {left:"2%"}}
     onClick ={()=>setExpended(!expended)}
     >
        <Icon icon={menu}/>
        </div>

    <motion.div className="sidebar" 
    variants={sidebarVariant} animate={window.innerWidth <= 768 ? `${expended}` : ''}  >

        <div className="logo">
            <Icon size={25} icon={user_circle}/>
            <span>Admin</span>
        </div>

       <div className="menu">
        {SidebarData.map((item,index) =>{
            return(
               <div className={selected === index ?"menuItem active" :"menuItem"}
               key={index}
               onClick={() => setSelected(index)}
               >
                {item.icon}
                <span>{item.head}</span>
               </div>
            )
        })};

<div className="menuItem">
        <Icon icon={logout}/>
        <span><a href="/Login" onClick={studentLogout}>Logout</a></span>
       </div>
       </div>

    </motion.div>
    </>
  )
}

export default Navbar;