import React from 'react';
import { Link } from 'react-router-dom';
import './registerStyle.css';
import {useFormik} from 'formik';
import * as yup from 'yup';
import axios from 'axios';
import { toast } from 'react-toastify';



function Register(props) {
  
  const formik = useFormik({
    initialValues : {
      sname:'',
      email:'',
      password:'',
      confirmPassword:'',
      
    },
    
    validationSchema : yup.object({
      sname:yup.string()
      .required("Name is required").strict().trim()
      .min(3,"Name is minimum 3 characters only")
      .max(15,"Name is maximum 15 characters only"),
      email:yup.string()
      .required("Email is required")
      .email(),
      password:yup.string()
      .required("Password is required"),
      confirmPassword:yup.string()
      .oneOf([yup.ref('password'),null],"Confirm passsword and password must be same")
      .required("Confirm password is required")
    }),
     
    onSubmit :(data) => {
      if (data) {
        axios.post("http://localhost:3500/api/sregister", data)
        .then(res => {
          toast.success("Successfully Registered !!!");
          localStorage.setItem('name',data.sname);
          props.history.push('/Login');
        });
        
      }}
  })
  return (
    <div className="container register">
      <div className="jumbotron">
      <form  autoComplete="off" className="regs" onSubmit={formik.handleSubmit} >
        <h2 className="head">Register here!</h2><hr />
        
        <div className="form-group" >
          <label  htmlFor="name" >Name:</label>
          <input type="text" 
          className="form-control" id="name" name="sname"
          value={formik.values.sname} 
          onChange={formik.handleChange}
          placeholder="enter your name" />
          {formik.errors.sname?<div className="text-danger">{formik.errors.sname}</div>:null}
        </div>

        <div className="form-group">
          <label  htmlFor="email" >Email:</label>
          <input type="text" className="form-control" 
          id="email" name="email"
          value={formik.values.email} 
          onChange={formik.handleChange} 
          onBlur={formik.handleBlur}
          placeholder="enter your email"/>
          {formik.errors.email?<div className="text-danger">{formik.errors.email}</div>:null}
        </div>

        <div className="form-group" >
          <label  htmlFor="pword">Password:</label>
          <input type="password" className="form-control" 
          id="pword" name="password"
          value={formik.values.password} 
          onChange={formik.handleChange}
          placeholder="enter your password"/>
          {formik.errors.password?<div className="text-danger">{formik.errors.password}</div>:null}
        </div>

        <div className="form-group" >
          <label htmlFor="cpword" > Conform password:</label>
          <input type="password" className="form-control" 
          id="cpword" name="confirmPassword"
          value={formik.values.confirmPassword} 
          onChange={formik.handleChange} 
          placeholder="enter your confirm password"/>
          {formik.errors.confirmPassword?<div className="text-danger">{formik.errors.confirmPassword}</div>:null}
        </div>

        <button className="btn btn-info form-group" type="submit" >Register</button>

        <div style={{marginTop:9}} className="form-group">
          <p>Already do you have account <b><Link className="login" to="/Login">Login</Link></b></p>
        </div>

      </form>
      </div>
    </div>
  );
};

export default Register;