import React, { useState, useEffect } from 'react';
import {Link} from 'react-router-dom';
import Navbar from './navbar/Navbar';
import './Homestyle.css';
import axios from 'axios';
import Topnav from './navbar/Topnav/Topnav';
import { toast } from 'react-toastify';
import Icon from 'react-icons-kit';
import {circle_plus} from 'react-icons-kit/ikons/circle_plus';

function Home() {
  const [data, setData] = useState([]);

  useEffect(() => {
    getcompliant();
  },[])

  
  const Delcompliant = async (_id) => {
    if (window.confirm("Are you sure delete the record")) {
      const response = await axios.delete(`http://localhost:3500/compliant/del/${_id}`);
      if(response.status === 200){
        toast.success("Compliant record deleted successfully ")
        getcompliant();
      }
       
    }
  }
  const getcompliant = async () => {
    await axios.get("http://localhost:3500/compliant/getcompliant").then(res => {
      setData(res.data);
    });



  }

  return (

    <div>
      <div><Navbar /></div>
      <div><Topnav /></div>
      <br/>
      <div className='Homepage'>
        <div className="head">
        <span >
          <h3> View Compliant Register</h3>
        </span>
        <span >
          <a type="submit" href="/Compliant" title="Add compliant"><Icon size={22} icon={circle_plus}/></a>
        </span>
        </div>
        <div className="table-responsive">
          <table className="styled-table">
            <thead>
              <tr>
                <th >Name</th>
                <th >Email</th>
                <th >Reg.Number</th>
                <th >Department</th>
                <th>ComDivison</th>
                <th>Description</th>
                <th>Action</th>
              </tr>
            </thead>
            <tbody>
              {data && data.map((item, index) => {
                return (
                  <tr key={index}>
                    <td>{item.sname}</td>
                    <td>{item.email}</td>
                    <td>{item.registernumber}</td>
                    <td>{item.department}</td>
                    <td>{item.compdivison}</td>
                    <td>{item.description}</td>
                    <td>
                      <div>
                        <Link to={`/update/${item._id}`}><button className="btn btn-info" style={{marginRight:1.9}} >Edit</button></Link>
                        <button className="btn btn-danger" onClick={()=> Delcompliant(item._id)}>Delete</button>
                      </div>
                    </td>
                  </tr>

                )
              })}
            </tbody>
          </table>
        </div>

      </div>
    </div>

  )
}

export default Home;