import React from 'react';
import './topnavstyle.css';
import Icon from 'react-icons-kit';
import { user } from 'react-icons-kit/fa/user';
import {DropdownButton,Dropdown} from 'react-bootstrap';





function Topnav() {

  const logout = () => {
    JSON.parse(localStorage.removeItem('auth'));
  }
  return (
  
      <div className="home">
        <div>
          <b><h3 className="cmp cms" >Compliant management system</h3></b>
        </div>
        <div className="cmp1">
        <DropdownButton title={<Icon size={22} icon={user} />}>
          <Dropdown.Item className="cmp cmp1" href="/Login" onClick={logout} title="Logout">Logout</Dropdown.Item>
          <Dropdown.Item href="#/action-2">Another action</Dropdown.Item>
          <Dropdown.Item href="#/action-3">Something else</Dropdown.Item>
        </DropdownButton>
        </div>
      </div>
  )
}

export default Topnav;