import './App.css';
import {BrowserRouter,Switch,Route} from "react-router-dom"; 
import Login from './components/Login';
import Register from './components/Register';
import Home from './components/Home';
import Protected from "./components/protected";
import{ToastContainer} from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Compliant from './components/Compliant';


function App() {
  return (
    <div className="App">
    <BrowserRouter>
      <Switch>
        <Route exact path="/cms-for-colleges-school" component={Login}/>
        <Route  path="cms-for-colleges-school/Register" component={Register}/>
        <Route  path="/Login" component={Login}/>
        <Protected exact path="cms-for-colleges-school/Home" component={Home}/>
        <Route  path="cms-for-colleges-school/update/:id" component={Compliant}/>
        <Route exact path="cms-for-colleges-school/Compliant" component={Compliant}/>
      </Switch>
    </BrowserRouter>
      <ToastContainer/>
    </div>
  );
}

export default App;
