import React, { useEffect, useState } from 'react';
import './Complant.css';
import Navbar from './navbar/Navbar';
import Topnav from './navbar/Topnav/Topnav';
import axios from 'axios';
import { toast } from 'react-toastify';
import { useParams, useHistory } from 'react-router-dom';





function Compliant() {

  const history = useHistory();

  const [detail, setDetail] = useState({
    sname: "",
    email: "",
    registernumber: "",
    department: "",
    compdivison: "",
    description: ""
  });


  const { id } = useParams();
  useEffect(() => {
    if (id) {
      getSingleUser(id);
    }
  }, [id]);


  const getSingleUser = async (id) => {
    const respose = await axios.get(`http://localhost:3500/compliant/compliant/${id}`);
    setDetail({ ...respose.data })
   // console.log(respose.data);
  };


  const addCompliant = async (detail) => {
    const respose = await axios.post("http://localhost:3500/compliant/compliant", detail);
    if (respose.status === 200) {
      setTimeout(() => history.push("/Home"), 500);
      toast.success("Successfully Registered !!!");
    }
  };

  const updateCompliant = async (detail) => {
    const respose = await axios.put(`http://localhost:3500/compliant/update/${id}`, detail);
    if (respose.status === 200) {
      setTimeout(() => history.push("/Home"), 500);
      toast.success("Successfully updated !!!");
    }
  };

  const handleSubmit = (data) => {
    data.preventDefault();
    if(!id){
      addCompliant(detail);
    }
    else{
      updateCompliant(detail);
    }
    

  };
  const handleChange = (data) => {
    setDetail({ ...detail, [data.target.name]: data.target.value });
  }

  return (
    <div className="compliant">
      <div><Navbar /></div>
      <div><Topnav /></div>
      <div className='compl'>
        <div className="container comp">
          <div >
          <h3 className="head">Compliant Register here!</h3>
            <form className="was-validated cform row" onSubmit={handleSubmit} autoComplete="off"><hr/>

              <div className="form-group  col-sm-6 " >
                <label htmlFor="name" >Name:</label>
                <input type="text"
                  className="form-control" id="name" name="sname"
                  placeholder="enter your name" required
                  onChange={handleChange}
                  value={detail.sname}
                />
                <div className="invalid-feedback">Please enter your name.</div>
              </div>

              <div className="form-group  col-sm-6 ">
                <label htmlFor="email" >Email:</label>
                <input type="text" className="form-control"
                  id="email" name="email"
                  value={detail.email}
                  onChange={handleChange}
                  placeholder="enter your email" required />
                <div className="invalid-feedback">Please enter your email.</div>
              </div>

              <div className="form-group col-sm-6 " >
                <label htmlFor="regnumber">Register number:</label>
                <input type="text" className="form-control"
                  id="regnumber" name="registernumber"
                  value={detail.registernumber}
                  onChange={handleChange}
                  placeholder="enter your register number" required />
                <div className="invalid-feedback">Please enter your Registernumber.</div>
              </div>

              <div className="form-group col-sm-6 " >
                <label htmlFor="dep" > Department:</label>
                <input type="text" className="form-control"
                  id="dep" name="department"
                  value={detail.department}
                  onChange={handleChange}
                  placeholder="enter your confirm department" required />
                <div className="invalid-feedback">Please enter your Department.</div>

              </div>

              <div className="form-group col-sm-6 " >
                <label htmlFor="compdiv" > Compliant Divison:</label>
                <select className="form-control " name="compdivison" id="compdiv" required
                  value={detail.compdivison}
                  onChange={handleChange}>
                  <option className="text-center" value="">-  -  -  -  -  -  -  -  Choose Divison-  -  -  -  -  -  -  -</option>
                  <option className="text-center" value="Mess Food">Mess Food</option>
                  <option className="text-center" value="HostelMaintanance">Hostel Maintanance</option>
                  <option className="text-center" value="Others">Others</option>
                </select>
                <div className="invalid-feedback">Please select one</div>
              </div>

              <div className="form-group col-sm-6" >
                <label htmlFor="descri">Description :</label>
                <textarea type="text" className="form-control"
                  id="descri" name="description"
                  value={detail.description}
                  onChange={handleChange}
                  placeholder="Type here................" required />
                <div className="invalid-feedback">Enter your compliant reason</div>
              </div>

              <div className="form-group col-sm-2">
                <button className="btn btn-info " type="submit" >{id ? "Update" : "Register"}</button>
              </div>


            </form>
          </div>

        </div>
      </div>
    </div>
  )
}

export default Compliant;