import React from 'react';
import './loginstyle.css';
import {useFormik} from 'formik';
import * as yup from 'yup';
import axios from 'axios';
import { toast } from 'react-toastify';

function Login(props) {
    const formik = useFormik({
        initialValues : {
          email:'',
          password:''
        },
        
        validationSchema : yup.object({
          email:yup.string()
          .required("Email is required")
          .email(),
          password:yup.string()
          .required("Password is required"),
         
        }),
         
        onSubmit :(data) => {
            axios.post("http://localhost:3500/api/login", data)
            .then(res => {
              localStorage.setItem('auth',JSON.stringify(res.data));
            toast.success("Login Successfully !!! ");
            props.history.push('./Home');
            })
            .catch(err =>{
                toast.error(err.response.data);
            })
    }
      });

  return (
    <div style={{marginTop:30}} className="container log">
      <div className="jumbotron">
        <form  autoComplete="off" onSubmit={formik.handleSubmit} >

            <h3 >Login</h3><hr/>

        <div className="form-group">
            <label className="labels"  htmlFor="email">Email :</label>
            <input className="form-control"
            value={formik.values.email} 
            onChange={formik.handleChange} 
             type="text" id="email" placeholder="enter your email" />
            {formik.errors.email?<div className="text-danger">{formik.errors.email}</div>:null}
        </div>

        <div className="form-group" >
          <label  htmlFor="pword">Password:</label>
          <input type="password" className="form-control" 
          id="pword" name="password"
          value={formik.values.password} 
          onChange={formik.handleChange}
          placeholder="enter your password"/>
          {formik.errors.password?<div className="text-danger">{formik.errors.password}</div>:null}
        </div>

        <div  className="mt-2">
            <p><a href="forgotpassword" style={{fontSize:18}}>Forgot passwrod?</a></p>
        </div>

        <button className="btn btn-success form-control" type="submit">Login</button>

        <div style={{marginTop:6}}className="reg mt-5">
            <p><b style={{fontSize:18}}>Not yet member? </b><a className="register" href="/Register">Register now</a></p>
        </div>

        </form>
    </div>
    </div>
  )
}

export default Login